
body {
  background: whitesmoke;
  word-break: break-all;
  word-wrap: break-word;
  font-weight: normal;
  font-size: 16px;
  overflow-x: hidden;
}
.dygraph-axis-label {
  font-size: 14px;
  font-family: 'Arial';
  color: #505050;
}
.dygraph-label {
  font-size: 12px;
  font-family: 'Arial';
  font-style: italic;
  color: #505050;
}
.dygraph-title {
  font-size: 14px;
  font-family: 'Arial';
  font-weight: bold;
  text-align: left;
  padding-left: 80px;
  font-style: normal;
  color: #000000;
}
.dygraph-legend {
  font-size: 14px;
  font-family: 'Arial';
  color: #505050;
}



